import React, { useState } from "react";
import { useNavigate } from 'react-router-dom';
import SwipeableViews from 'react-swipeable-views';
import { useTheme } from "./ThemeProvider";
import style_bb from "./css/BottomBar.module.css";

const Container = (props) => {
  const [ThemeMode, toggleTheme] = useTheme();
  const [index, setIndex] = useState(0);

  return (
    <>
      <SwipeableViews index={index} axis="y" resistance disabled={true} containerStyle={{height: "100vh"}} style={{backgroundColor: ThemeMode === "dark" ? "rgba(50, 50, 50, 1)" : "rgba(215, 215, 215, 1)"}}>
        {props.components}
      </SwipeableViews>
      <BottomBar link={props.link} index={index} setIndex={setIndex} indexMax={props.indexMax} />
    </>
  );
};

const BottomBar = (props) => {
  const [ThemeMode, toggleTheme] = useTheme();
  const navigate = useNavigate();
  const DecreaseIndex = () => props.index > 0 ? props.setIndex(props.index - 1) : props.index;
  const IncreaseIndex = () => props.index < props.indexMax - 1 ? props.setIndex(props.index + 1) : props.index;
  const Color = { color: ThemeMode === "dark" ? "rgba(225, 225, 225, 1)" : "rgba(32, 32, 32, 1)" };

  return (
    <div className={style_bb.container} style={{backgroundColor: ThemeMode === "dark" ? "rgba(32, 32, 32, 1)" : "rgba(240, 240, 240, 1)", borderTopWidth: ThemeMode === "dark" ? 0.35 : 0.5, borderTopStyle: "solid", borderTopColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)"}}>
      <div className={style_bb.flex_container}>
        <div onClick={toggleTheme} className={style_bb.flex_center}>
          <span class="material-icons" style={Color}>lightbulb_outline</span>
        </div>
      </div>
      <div className={style_bb.flex_container}>
        <div onClick={DecreaseIndex} className={style_bb.flex_center}>
          <span class="material-icons" style={Color}>arrow_upward</span>
        </div>
      </div>
      <div className={style_bb.flex_container}>
        <div onClick={IncreaseIndex} className={style_bb.flex_center}>
          <span class="material-icons" style={Color}>arrow_downward</span>
        </div>
      </div>
      <div className={style_bb.flex_container}>
        <div onClick={() => navigate(-1)} className={`${style_bb.flex_center} ${style_bb.link_none}`}>
          <span class="material-icons" style={Color}>last_page</span>
        </div>
      </div>
    </div>
  );
};

export default Container;
