import React, { useEffect, useRef } from "react";
import { useTheme } from "../ThemeProvider";
import style from "./css/Component_Study_Writing.module.css";
import Container from "../Container";
import CanvasDraw from "react-canvas-draw";

const Component = (props) => {
  const [ThemeMode, toggleTheme] = useTheme();
  const number = {zIndex: 3, backgroundColor: ThemeMode === "dark" ? "rgba(32, 32, 32, 1)" : "rgba(265, 228, 158, 1)", color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)", borderBottomStyle: "solid", borderBottomColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderBottomWidth: ThemeMode === "dark" ? 1 : 2, borderRightStyle: "solid", borderRightColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderRightWidth: ThemeMode === "dark" ? 1 : 2};
  const clear = {zIndex: 3, backgroundColor: ThemeMode === "dark" ? "rgba(32, 32, 32, 1)" : "rgba(265, 228, 158, 1)", borderBottomStyle: "solid", borderBottomColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderBottomWidth: ThemeMode === "dark" ? 1 : 2, borderLeftStyle: "solid", borderLeftColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderLeftWidth: ThemeMode === "dark" ? 1 : 2};
  const border_top = {borderTopStyle: "solid", borderTopColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderTopWidth: ThemeMode === "dark" ? 1 : 2};
  const background_color1 = {backgroundColor: ThemeMode === "dark" ? "rgba(22, 22, 22, 1)" : "rgba(250, 250, 250, 1)"};
  const background_color2 = {backgroundColor: ThemeMode === "dark" ? "rgba(22, 22, 22, 1)" : "rgba(294, 243, 252, 1)"};
  const background_color3 = {backgroundColor: ThemeMode === "dark" ? "rgba(22, 22, 22, 1)" : "rgba(195, 227, 252, 1)"};
  const text1 = {color: ThemeMode === "dark" ? "rgba(255, 255, 255, 0.3)" : "rgba(0, 153, 0, 0.3)"};
  const text2 = {color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)"};
  const text3 = {color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)"};
  const Color = {color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgba(32, 32, 32, 1)", fontSize: 21};

  const canvasRef = useRef();

  useEffect(() => {
    canvasRef.current.clear();
  }, [ThemeMode]);

  return (
    <Container>
      <div className={style.number} style={number}>{props.ST}</div>
      <div className={style.clear} style={clear} onClick={() => canvasRef.current.clear()}>
        <span class="material-icons" style={Color}>delete_outline</span>
      </div>
      <div className={style.container}>
        <div className={style.flex_container1} style={background_color1}>
          <div className={style.text1} style={text1}>{props.text1}</div>
          <CanvasDraw ref={canvasRef} brushColor={ThemeMode === "dark" ? "rgba(250, 250, 250, 1)" : "#212121"} brushRadius={7} enablePanAndZoom={true} hideInterface={true} hideGrid={true} style={background_color1} />
        </div>
        <div className={style.flex_container2} style={border_top}>
          <div className={style.flex_container2_1} style={background_color2}>
            <div className={style.text2} style={text2}>{props.text2}</div>
          </div>
          <div className={style.flex_container2_2} style={Object.assign({}, background_color3, border_top)}>
            <div className={style.text3} style={text3}>{props.text3}</div>
          </div>
        </div>
      </div>
    </Container>
  );
};

export default Component;
