import React, { useEffect } from "react";
import { Link } from 'react-router-dom';
import style from "./css/MoreDependentIndex.module.css"

const StudyDependentIndex = () => {
  return (
    <div className={style.container}>
      <div className={style.flex2}>
        <Link to="/StudyIndex" className={style.button}>훈 음 익 히 기</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/StudyIndex_ReviewType" className={style.button}>훈 음 복 습</Link>
      </div>
      <div className={style.flex2}>
        <Link to="/StudyIndex_Writing" className={style.button}>훈 음 쓰 기</Link>
      </div>
    </div>
  );
};

export default StudyDependentIndex;
