import React, { useState } from "react";
import SwipeableViews from "react-swipeable-views";
import styles from "./css/Modal.module.css";

function Modal(props) {
  const closeModal = () => props.setModalOpen(false);
  const openWeb = () => props.setWebValue(true);
  const Color = {fontSize: 26, color: "rgba(12, 12, 12, 1)"};

  return (
    <div className={styles.container}>
      <span class="material-icons modal_close" onClick={closeModal} style={Color}>close</span>
      <div className={styles.flex1}>
        <button onClick={openWeb} className={styles.button_homepage}>홈페이지</button>
      </div>
      <div className={styles.flex2}>
        <div className={styles.top_text}>다른 앱 더 둘러보기<span className={styles.profile_text_span}>(좌우 스와이프 가능)</span></div>
        <SwipeableViews enableMouseEvents className={styles.profile_flex_container}>
            <a href="https://play.google.com/store/apps/details?id=com.ongaram.sajasohag" className={styles.profile_container}>
                <img src={require("../assets/images/icon/icon_sajasohag.jpg")} alt="사자소학" className={styles.profile_image} />
                <div className={styles.profile_text}>사자소학</div>
            </a>
            <a href="https://play.google.com/store/apps/details?id=com.ongaram.cheonjamun" className={styles.profile_container}>
                <img src={require("../assets/images/icon/icon_cheonjamun.jpg")} alt="천자문" className={styles.profile_image} />
                <div className={styles.profile_text}>천자문</div>
            </a>
            <a href="https://play.google.com/store/apps/details?id=com.ongaram.chugujib" className={styles.profile_container}>
                <img src={require("../assets/images/icon/icon_chugujib.jpg")} alt="추구집" className={styles.profile_image} />
                <div className={styles.profile_text}>추구집</div>
            </a>
            <a href="https://play.google.com/store/apps/details?id=com.ongaram.bucks11" className={styles.profile_container}>
                <img src={require("../assets/images/icon/icon_jeokcheonsu.jpg")} alt="적천수" className={styles.profile_image} />
                <div className={styles.profile_text}>적천수</div>
            </a>
            <a href="https://play.google.com/store/apps/details?id=com.ongaram.japyeongjinjeon" className={styles.profile_container}>
                <img src={require("../assets/images/icon/icon_hageojib.jpg")} alt="학어집" className={styles.profile_image} />
                <div className={styles.profile_text}>학어집</div>
            </a>
            <a href="https://play.google.com/store/apps/details?id=com.ongaram.gyemong" className={styles.profile_container}>
                <img src={require("../assets/images/icon/icon_gyemong.jpg")} alt="계몽편" className={styles.profile_image} />
                <div className={styles.profile_text}>계몽편</div>
            </a>
            <a href="https://play.google.com/store/apps/details?id=com.ongaram.pageum" className={styles.profile_container}>
                <img src={require("../assets/images/icon/icon_samjagyeong.jpg")} alt="삼자경" className={styles.profile_image} />
                <div className={styles.profile_text}>삼자경</div>
            </a>
        </SwipeableViews>
      </div>
    </div>
  );
}
export default Modal;
