import React, { useState } from "react";
import SwipeableViews from 'react-swipeable-views';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { useTheme } from "../ThemeProvider";
import style from "./css/Component_Word.module.css";
import Container from "../Container";
import TabText from "../swipeable_views/TabText";
import QI from "../../assets/images/q.png";
import _QI from "../../assets/images/_q.png";
import AI from "../../assets/images/a.png";
import _AI from "../../assets/images/_a.png";

const Component = (props) => {
  const [ThemeMode, toggleTheme] = useTheme();
  const [index, setIndex] = useState(0);
  const [V1, setV1] = useState("Q");
  const [V2, setV2] = useState("Q");
  const [V3, setV3] = useState("Q");
  const [V4, setV4] = useState("Q");
  const [V5, setV5] = useState("Q");
  const [V6, setV6] = useState("Q");
  const handleChange = (e, v) => setIndex(v);
  const handleChangeIndex = (i) => setIndex(i);
  const QImage = ThemeMode === "dark" ? _QI : QI;
  const AImage = ThemeMode === "dark" ? _AI : AI;
  const background_color = {backgroundColor: ThemeMode === "dark" ? "rgba(22, 22, 22, 1)" : "rgba(250, 250, 250, 1)"};
  const top_text_container = {backgroundColor: ThemeMode === "dark" ? "rgba(32, 32, 32, 1)" : "rgba(265, 228, 158, 1)", borderBottomStyle: "solid", borderBottomColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderBottomWidth: ThemeMode === "dark" ? 1 : 2};
  const TabsStyle = {position: "absolute", top: 50, right: 0, width: "100%", height: 50, backgroundColor: ThemeMode === "dark" ? "rgba(32, 32, 32, 1)" : "rgba(250, 250, 250, 1)", borderBottomStyle: "solid", borderBottomColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderBottomWidth: ThemeMode === "dark" ? 1 : 2};
  const TabIndicatorProps = {height: ThemeMode === "dark" ? 1.85 : 2.25, backgroundColor: ThemeMode === "dark" ? "rgba(200, 200, 200, 1)" : "rgba(200, 160, 12, 1)", marginBottom: ThemeMode === "dark" ? 1 : 0.75};
  const C = "tab_text";
  const S_i = {color: ThemeMode === "dark" ? "rgba(200, 200, 200, 0.8)" : "rgba(108, 108, 108, 0.8)"};
  const S_a = {color: ThemeMode === "dark" ? "rgba(200, 200, 200, 1)" : "rgba(200, 160, 12, 1)"};
  const border_top = {borderTopColor: ThemeMode === "dark" ? "rgba(128, 128, 128, 1)" : "gray", borderTopStyle: "solid", borderTopWidth: ThemeMode === "dark" ? 0.5 : 2};
  const border_light_top = {borderTopColor: ThemeMode === "dark" ? "rgba(128, 128, 128, 1)" : "gray", borderTopStyle: "solid", borderTopWidth: ThemeMode === "dark" ? 0.5 : 1.75};
  const border_right = {borderRightColor: ThemeMode === "dark" ? "rgba(128, 128, 128, 1)" : "gray", borderRightStyle: "solid", borderRightWidth: ThemeMode === "dark" ? 0.5 : 2};
  const text_green = {color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgba(0, 153, 0, 1)"};
  const text_brown = {color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)"};
  const pink = {backgroundColor: ThemeMode === "dark" ? "transparent" : "rgba(294, 243, 252, 1)"};
  const skyblue = {backgroundColor: ThemeMode === "dark" ? "transparent" : "rgba(195, 227, 252, 1)"};
  const review_circle = {backgroundColor: ThemeMode === "dark" ? "rgba(245, 245, 245, 1)" : "transparent", padding: ThemeMode === "dark" ? 20 : 0, borderRadius: ThemeMode === "dark" ? "50%" : 0};
  const review_circle_small = {backgroundColor: ThemeMode === "dark" ? "rgba(245, 245, 245, 1)" : "transparent", padding: ThemeMode === "dark" ? 10 : 0, borderRadius: ThemeMode === "dark" ? "50%" : 0};

  return (
    <Container>
      <img className="review_position" src={_QI} />
      <img className="review_position" src={_AI} />
      <div className={style.number} style={{backgroundColor: ThemeMode === "dark" ? "rgba(32, 32, 32, 1)" : "rgba(265, 228, 158, 1)", color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)", borderBottomStyle: "solid", borderBottomColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderBottomWidth: ThemeMode === "dark" ? 1 : 2}}>{props.ST}</div>
      <div className={style.top_text_container} style={top_text_container}>
        <div className={`${style.top_text_item} ${style.top_text1}`} style={{color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)"}}>{props.CT}</div>
        <div className={`${style.top_text_item} ${style.top_text2}`} style={{color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)"}}>{props.NTB}</div>
        <div className={`${style.top_text_item} ${style.top_text3}`} style={{color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)"}}>{props.NT}</div>
      </div>
      <Tabs value={index} fullWidth onChange={handleChange} style={TabsStyle} TabIndicatorProps={{style:TabIndicatorProps}}>
        <Tab label={index === 0 ? <TabText T="단어" C={C} S={S_a} /> : <TabText T="단어" C={C} S={S_i} />} className={style.tab_item} />
        <Tab label={index === 1 ? <TabText T="그 외" C={C} S={S_a} /> : <TabText T="그 외" C={C} S={S_i} />} className={style.tab_item} />
      </Tabs>
      <SwipeableViews index={index} enableMouseEvents onChangeIndex={handleChangeIndex} className="swipeable_views_container_top_text">
        <div className={`${style.container} ${style.row}`} style={background_color}>
          <div className={`${style.flex} ${style.column}`} style={border_right}>
            <div className={`${style.flex} ${style.flex_center} ${style.Word1_Word}`} style={Object.assign({}, text_green, pink)}>{props.Word1}</div>
            <div className={`${style.flex} ${style.flex_center} ${style.Word1_Word}`} style={Object.assign({}, text_green, skyblue, border_top)}>{props.Word2}</div>
            <div className={`${style.flex} ${style.flex_center} ${style.Word1_Word}`} style={Object.assign({}, text_green, pink, border_top)}>{props.Word3}</div>
          </div>
          <div className={`${style.flex} ${style.column}`}>
            <div className={`${style.flex} ${style.flex_center}`} style={Object.assign({}, text_brown, skyblue)}>
              {V1 === "Q" ? <div style={review_circle_small}><img className={style.image} src={AImage} onClick={() => setV1("A")} /></div> : <span className={style.Word1_WordSound}onClick={() => setV1("Q")}>{props.WordSound1}</span>}
            </div>
            <div className={`${style.flex} ${style.flex_center}`} style={Object.assign({}, text_brown, pink, border_top)}>
              {V2 === "Q" ? <div style={review_circle_small}><img className={style.image} src={AImage} onClick={() => setV2("A")} /></div> : <span className={style.Word1_WordSound}onClick={() => setV2("Q")}>{props.WordSound2}</span>}
            </div>
            <div className={`${style.flex} ${style.flex_center}`} style={Object.assign({}, text_brown, skyblue, border_top)}>
              {V3 === "Q" ? <div style={review_circle_small}><img className={style.image} src={AImage} onClick={() => setV3("A")} /></div> : <span className={style.Word1_WordSound}onClick={() => setV3("Q")}>{props.WordSound3}</span>}
            </div>
          </div>
        </div>
        <div className={`${style.container} ${style.row}`} style={background_color}>
          <div className={`${style.flex} ${style.column}`} style={border_right}>
            <div className={`${style.flex} ${style.flex_center} ${style.CT}`} style={text_green}>
              <span>{props.CT1}</span>
            </div>
            <div className={`${style.flex} ${style.flex_center} ${style.CT}`} style={Object.assign({}, text_green, border_top)}>
              <span>{props.CT2}</span>
            </div>
            <div className={`${style.flex} ${style.flex_center} ${style.CT}`} style={Object.assign({}, text_green, border_top)}>
              <span>{props.CT3}</span>
            </div>
          </div>
          <div className={`${style.flex} ${style.column}`}>
            <div className={`${style.flex} ${style.column} ${style.flex_center}`}>
              {V4 === "Q" ? (
                <div className={style.image_container} style={pink}>
                  <div style={review_circle_small}>
                    <img className={style.image} src={AImage} onClick={() => setV4("A")} />
                  </div>
                </div>
              ) : (
                <>
                  <div className={`${style.NTB} ${style.flex} ${style.flex_center}`} style={Object.assign({}, text_brown, pink)}>
                    <span onClick={() => setV4("Q")}>{props.NTB1}</span>
                  </div>
                  <div className={`${style.NT} ${style.flex} ${style.flex_center}`} style={Object.assign({}, text_brown, skyblue, border_light_top)}>
                    <span onClick={() => setV4("Q")}>{props.NT1}</span>
                  </div>
                </>
              )}
            </div>
            <div className={`${style.flex} ${style.column} ${style.flex_center}`} style={border_top}>
              {V5 === "Q" ? (
                <div className={style.image_container} style={skyblue}>
                  <div style={review_circle_small}>
                    <img className={style.image} src={AImage} onClick={() => setV5("A")} />
                  </div>
                </div>
              ) : (
                <>
                  <div className={`${style.NTB} ${style.flex} ${style.flex_center}`} style={Object.assign({}, text_brown, pink)}>
                    <span onClick={() => setV5("Q")}>{props.NTB2}</span>
                  </div>
                  <div className={`${style.NT} ${style.flex} ${style.flex_center}`} style={Object.assign({}, text_brown, skyblue, border_light_top)}>
                    <span onClick={() => setV5("Q")}>{props.NT2}</span>
                  </div>
                </>
              )}
            </div>
            <div className={`${style.flex} ${style.column} ${style.flex_center}`} style={border_top}>
              {V6 === "Q" ? (
                <div className={style.image_container} style={pink}>
                  <div style={review_circle_small}>
                    <img className={style.image} src={AImage} onClick={() => setV6("A")} />
                  </div>
                </div>
              ) : (
                <>
                  <div className={`${style.NTB} ${style.flex} ${style.flex_center}`} style={Object.assign({}, text_brown, pink)}>
                    <span onClick={() => setV6("Q")}>{props.NTB3}</span>
                  </div>
                  <div className={`${style.NT} ${style.flex} ${style.flex_center}`} style={Object.assign({}, text_brown, skyblue, border_light_top)}>
                    <span onClick={() => setV6("Q")}>{props.NT3}</span>
                  </div>
                </>
              )}
            </div>
          </div>
        </div>
      </SwipeableViews>
    </Container>
  );
};

export default Component;
