import React from "react";
import Component from "../../function/idiom/Component_Idiom_Writing";
import Container from "../../function/Information_Writing";
import Data from "../../assets/data/Data_Idiom";

const Idiom_Writing6 = () => {
  const datas = Data.filter(num => num.id >= 251).filter(num => num.id <= 300);
  const components = datas.map((data, i) => <Component key={i} ST={data.id} CT={data.CT} NTB={data.NTB} NT={data.NT} CT1={data.CT1} CT2={data.CT2} CT3={data.CT3} CT4={data.CT4} CT5={data.CT5} CT6={data.CT6} CT7={data.CT7} CT8={data.CT8} NT1={data.NT1} NT2={data.NT2} NT3={data.NT3} NT4={data.NT4} NT5={data.NT5} NT6={data.NT6} NT7={data.NT7} NT8={data.NT8} />);

  return (
    <Container components={components} indexMax={datas.length} />
  );
};

export default Idiom_Writing6;
