import React, { useEffect } from "react";
import { Link } from 'react-router-dom';
import style from "./css/MoreDependentIndex.module.css"

const IdiomDependentIndex = () => {
  return (
    <div className={style.container}>
      <div className={style.flex2}>
        <Link to="/IdiomIndex" className={style.button}>사자성어 익히기</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/IdiomIndex_ReviewType" className={style.button}>사자성어 복습</Link>
      </div>
      <div className={style.flex2}>
        <Link to="/IdiomIndex_Writing" className={style.button}>사자성어 쓰기</Link>
      </div>
    </div>
  );
};

export default IdiomDependentIndex;
