import React from "react";
import style from "../css/Copyright.module.css"

const Copyright1 = () => {
  const copyright1 = `저작권이 만료된 책으로 자유 이용이 가능하여 앱으로 인용함.`;

  return (
    <div className={style.container}>
      <div className={`${style.text} ${style.min_h}`}>{copyright1}</div>
    </div>
  );
};

export default Copyright1;
