import React from "react";
import { Link } from 'react-router-dom';
import style from "../css/MoreIndex_ReviewType.module.css"

const WordIndex_ReviewType = () => {
  return (
    <div className={style.container}>
      <div className={style.flex1}>
        <Link to="/WordIndex_ReviewA" className={style.button}>A type</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/WordIndex_ReviewB" className={style.button}>B type</Link>
      </div>
    </div>
  );
};

export default WordIndex_ReviewType;
