import React, { useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination } from "swiper";
import "swiper/css";
import "swiper/css/pagination";
import style from "../css/MoreIndex.module.css"
import Button from "../../function/IndexButton";
import dataN from "../../assets/data/button/button_number_proverb.json";
import dataL from "../../assets/data/button/proverb/button_link_proverb.json";

const ProverbIndex = () => {
  return (
    <Swiper pagination={true} modules={[Pagination]} className={style.container}>
      <SwiperSlide className={style.flex_container}>
        <div className={style.flex_item}>
          <Button to={dataL.L1} text={dataN.N1}/>
          <Button to={dataL.L3} text={dataN.N3}/>
          <Button to={dataL.L5} text={dataN.N5}/>
          <Button to={dataL.L7} text={dataN.N7}/>
          <Button to={dataL.L9} text={dataN.N9}/>
          <Button to={dataL.L11} text={dataN.N11}/>
        </div>
        <div className={style.flex_item}>
          <Button to={dataL.L2} text={dataN.N2}/>
          <Button to={dataL.L4} text={dataN.N4}/>
          <Button to={dataL.L6} text={dataN.N6}/>
          <Button to={dataL.L8} text={dataN.N8}/>
          <Button to={dataL.L10} text={dataN.N10}/>
          <Button to={dataL.L12} text={dataN.N12}/>
        </div>
      </SwiperSlide>
      <SwiperSlide className={style.flex_container}>
        <div className={style.flex_item}>
          <Button to={dataL.L13} text={dataN.N13}/>
          <Button to={dataL.L15} text={dataN.N15}/>
          <Button to={dataL.L17} text={dataN.N17}/>
        </div>
        <div className={style.flex_item}>
          <Button to={dataL.L14} text={dataN.N14}/>
          <Button to={dataL.L16} text={dataN.N16}/>
          <Button to={dataL.L18} text={dataN.N18}/>
        </div>
      </SwiperSlide>
    </Swiper>
  );
};

export default ProverbIndex;
