import React from "react";
import Component from "../../function/word/Component_Word_Writing";
import Container from "../../function/Information_Writing";
import Data from "../../assets/data/Data_Word";

const Word_Writing10 = () => {
  const datas = Data.filter(num => num.id >= 451).filter(num => num.id <= 500);
  const components = datas.map((data, i) => <Component key={i} ST={data.id} CT={data.CT} NTB={data.NTB} NT={data.NT} Word1={data.Word1} Word2={data.Word2} Word3={data.Word3} WordSound1={data.WordSound1} WordSound2={data.WordSound2} WordSound3={data.WordSound3} />);

  return (
    <Container components={components} indexMax={datas.length} />
  );
};

export default Word_Writing10;
