import React, { useEffect } from "react";
import style from "../css/MoreIndex.module.css"
import Button from "../../function/IndexButton_original_gyemong";
import dataN from "../../assets/data/button/button_number_original.json";
import dataL from "../../assets/data/button/original/button_link_original_gyemong.json";

const OriginalIndex_gyemong = () => {
  return (
    <div className={`${style.container} ${style.flex_container}`}>
      <div className={style.flex_item}>
        <Button to={dataL.L1} text={dataN.N1}/>
        <Button to={dataL.L2} text={dataN.N2}/>
        <Button to={dataL.L3} text={dataN.N3}/>
        <Button to={dataL.L4} text={dataN.N4}/>
        <Button to={dataL.L5} text={dataN.N5}/>
      </div>
    </div>
  );
};

export default OriginalIndex_gyemong;
