import React from "react";
import Component from "../../function/idiom/Component_Idiom_ReviewB";
import Container from "../../function/Information";
import Data from "../../assets/data/Data_Idiom";

const Idiom_ReviewB7 = () => {
  const datas = Data.filter(num => num.id >= 301).filter(num => num.id <= 350);
  const components = datas.map((data, i) => <Component key={i} ST={data.id} CT={data.CT} NTB={data.NTB} NT={data.NT} Idiom1={data.Idiom1} IdiomSound1={data.IdiomSound1} IdiomMeaning1={data.IdiomMeaning1} Idiom2={data.Idiom2} IdiomSound2={data.IdiomSound2} IdiomMeaning2={data.IdiomMeaning2} CT1={data.CT1} CT2={data.CT2} CT3={data.CT3} CT4={data.CT4} CT5={data.CT5} CT6={data.CT6} CT7={data.CT7} CT8={data.CT8} NTB1={data.NTB1} NTB2={data.NTB2} NTB3={data.NTB3} NTB4={data.NTB4} NTB5={data.NTB5} NTB6={data.NTB6} NTB7={data.NTB7} NTB8={data.NTB8} NT1={data.NT1} NT2={data.NT2} NT3={data.NT3} NT4={data.NT4} NT5={data.NT5} NT6={data.NT6} NT7={data.NT7} NT8={data.NT8} />);

  return (
    <Container components={components} />
  );
};

export default Idiom_ReviewB7;
