import React from "react";
import Component from "../../function/study/Component_Study_Writing";
import Container from "../../function/Information_Writing";
import Data from "../../assets/data/Data_Study";

const Study_Writing37 = () => {
  const datas = Data.filter(num => num.id >= 1801).filter(num => num.id <= 1850);
  const components = datas.map((data, i) => <Component key={i} ST={data.id} text1={data.CT} text2={data.NTB} text3={data.NT} />);

  return (
    <Container components={components} indexMax={datas.length} />
  );
};

export default Study_Writing37;
