import React from "react";
import { useTheme } from "./ThemeProvider";
import style from "./css/Container.module.css";

const Container = ({ children }) => {
  const [ThemeMode, toggleTheme] = useTheme();

  return (
    <div className={style.container}>
      <div className={style.flex_container} style={{borderColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)",borderStyle: "solid",borderWidth: ThemeMode === "dark" ? 1 : 2}}>{children}</div>
    </div>
  );
};

export default Container;
