import React, { useEffect } from "react";
import { Link } from 'react-router-dom';
import style from "../css/MoreIndex_ReviewType.module.css"

const StudyIndex_ReviewType = () => {
  return (
    <div className={style.container}>
      <div className={style.flex1}>
        <Link to="/StudyIndex_ReviewA" className={style.button}>A type</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/StudyIndex_ReviewB" className={style.button}>B type</Link>
      </div>
    </div>
  );
};

export default StudyIndex_ReviewType;
