import React, { useEffect } from "react";
import { Link } from 'react-router-dom';
import style from "./css/DependentIndex.module.css"

const CopyrightIndex = () => {
  return (
    <div className={style.container_copyright}>
      <div className={style.flex1}>
        <Link to="/Copyright1" className={style.button_small}>1. 이용 조건</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/Copyright2" className={style.button_small}>2. 폰트</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/Copyright3" className={style.button_small}>3. 아이콘</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/Copyright4" className={style.button_small}>4. 이미지</Link>
      </div>
    </div>
  );
};

export default CopyrightIndex;
