import React from "react";
import { Link } from 'react-router-dom';
import style from "./css/Home.module.css"

const Home = () => {
  return (
    <div className={style.container}>
      <div className={style.text_container}>
        <div className={style.text}>한 자 공 부</div>
      </div>
      <div className={style.semi_container}>
        <Link className={style.semi_image_container} to="/Index">시 작 하 기</Link>
      </div>
    </div>
  );
};

export default Home;
