import React, { useEffect, useRef } from "react";
import { useTheme } from "../ThemeProvider";
import style from "./css/Component_Idiom_Writing.module.css";
import Container from "../Container";
import CanvasDraw from "react-canvas-draw";

const Component = (props) => {
  const [ThemeMode, toggleTheme] = useTheme();
  const top_text_container = {backgroundColor: ThemeMode === "dark" ? "rgba(32, 32, 32, 1)" : "rgba(265, 228, 158, 1)", borderBottomStyle: "solid", borderBottomColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderBottomWidth: ThemeMode === "dark" ? 1 : 2};
  const clear = {backgroundColor: ThemeMode === "dark" ? "rgba(32, 32, 32, 1)" : "rgba(265, 228, 158, 1)", borderBottomStyle: "solid", borderBottomColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderBottomWidth: ThemeMode === "dark" ? 1 : 2, borderRightStyle: "solid", borderRightColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderRightWidth: ThemeMode === "dark" ? 1 : 2};
  const border_top = {borderTopStyle: "solid", borderTopColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderTopWidth: ThemeMode === "dark" ? 1 : 2};
  const background_color = {backgroundColor: ThemeMode === "dark" ? "rgba(22, 22, 22, 1)" : "rgba(250, 250, 250, 1)", height: "100%"};
  const border_right = {borderRightStyle: "solid", borderRightColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderRightWidth: ThemeMode === "dark" ? 1 : 2};
  const sound = {backgroundColor: ThemeMode === "dark" ? "rgba(22, 22, 22, 1)" : "rgba(250, 250, 250, 1)", color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgba(0, 153, 0, 0.7)", borderTopStyle: "solid", borderTopColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderTopWidth: ThemeMode === "dark" ? 1 : 1, borderLeftStyle: "solid", borderLeftColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderLeftWidth: ThemeMode === "dark" ? 1 : 1};
  const text = {color: ThemeMode === "dark" ? "rgba(255, 255, 255, 0.3)" : "rgba(0, 153, 0, 0.3)"};
  const Color = {color: ThemeMode === "dark" ? "rgba(225, 225, 225, 1)" : "rgba(32, 32, 32, 1)", fontSize: 17};
  const canvas = {backgroundColor: "transparent", height: "100%"};

  const canvasRef1 = useRef();
  const canvasRef2 = useRef();
  const canvasRef3 = useRef();
  const canvasRef4 = useRef();
  const canvasRef5 = useRef();
  const canvasRef6 = useRef();
  const canvasRef7 = useRef();
  const canvasRef8 = useRef();

  useEffect(() => {
    canvasRef1.current.clear();
    canvasRef2.current.clear();
    canvasRef3.current.clear();
    canvasRef4.current.clear();
    canvasRef5.current.clear();
    canvasRef6.current.clear();
    canvasRef7.current.clear();
    canvasRef8.current.clear();
  }, [ThemeMode]);

  return (
    <Container>
      <div className={style.number} style={{backgroundColor: ThemeMode === "dark" ? "rgba(32, 32, 32, 1)" : "rgba(265, 228, 158, 1)", color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)", borderBottomStyle: "solid", borderBottomColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderBottomWidth: ThemeMode === "dark" ? 1 : 2}}>{props.ST}</div>
      <div className={style.clear} style={clear} onClick={() => {canvasRef1.current.clear();canvasRef2.current.clear();canvasRef3.current.clear();canvasRef4.current.clear();canvasRef5.current.clear();canvasRef6.current.clear();canvasRef7.current.clear();canvasRef8.current.clear();}}>
        <span class="material-icons" style={Color}>delete_outline</span>
      </div>
      <div className={style.top_text_container} style={top_text_container}>
        <div className={`${style.top_text_item} ${style.top_text1}`} style={{color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)"}}>{props.CT}</div>
        <div className={`${style.top_text_item} ${style.top_text2}`} style={{color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)"}}>{props.NTB}</div>
        <div className={`${style.top_text_item} ${style.top_text3}`} style={{color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)"}}>{props.NT}</div>
      </div>
      <div className={style.container}>
        <div className={style.flex_item}>
          <div className={style.flex_container} style={Object.assign({}, background_color, border_right)}>
            <div className={style.text} style={text}>{props.CT1}</div>
            <div className={`${style.sound} ${style.border_bottom_right_round}`} style={sound}>{props.NT1}</div>
            <CanvasDraw ref={canvasRef1} className={style.canvas} brushColor={ThemeMode === "dark" ? "rgba(250, 250, 250, 1)" : "#212121"} brushRadius={2.5} enablePanAndZoom={true} hideInterface={true} hideGrid={true} style={canvas} />
          </div>
          <div className={style.flex_container} style={Object.assign({}, background_color, border_top, border_right)}>
            <div className={style.text} style={text}>{props.CT2}</div>
            <div className={`${style.sound} ${style.border_bottom_right_round}`} style={sound}>{props.NT2}</div>
            <CanvasDraw ref={canvasRef2} className={style.canvas} brushColor={ThemeMode === "dark" ? "rgba(250, 250, 250, 1)" : "#212121"} brushRadius={2.5} enablePanAndZoom={true} hideInterface={true} hideGrid={true} style={canvas} />
          </div>
          <div className={style.flex_container} style={Object.assign({}, background_color, border_top, border_right)}>
            <div className={style.text} style={text}>{props.CT3}</div>
            <div className={`${style.sound} ${style.border_bottom_right_round}`} style={sound}>{props.NT3}</div>
            <CanvasDraw ref={canvasRef3} className={style.canvas} brushColor={ThemeMode === "dark" ? "rgba(250, 250, 250, 1)" : "#212121"} brushRadius={2.5} enablePanAndZoom={true} hideInterface={true} hideGrid={true} style={canvas} />
          </div>
          <div className={`${style.flex_container} ${style.border_bottom_left_round}`} style={Object.assign({}, background_color, border_top, border_right)}>
            <div className={style.text} style={text}>{props.CT4}</div>
            <div className={`${style.sound} ${style.border_bottom_right_round}`} style={sound}>{props.NT4}</div>
            <CanvasDraw ref={canvasRef4} className={style.canvas} brushColor={ThemeMode === "dark" ? "rgba(250, 250, 250, 1)" : "#212121"} brushRadius={2.5} enablePanAndZoom={true} hideInterface={true} hideGrid={true} style={canvas} />
          </div>
        </div>
        <div className={style.flex_item}>
          <div className={style.flex_container} style={background_color}>
            <div className={style.text} style={text}>{props.CT5}</div>
            <div className={`${style.sound} ${style.border_bottom_right_round}`} style={sound}>{props.NT5}</div>
            <CanvasDraw ref={canvasRef5} className={style.canvas} brushColor={ThemeMode === "dark" ? "rgba(250, 250, 250, 1)" : "#212121"} brushRadius={2.5} enablePanAndZoom={true} hideInterface={true} hideGrid={true} style={canvas} />
          </div>
          <div className={style.flex_container} style={Object.assign({}, background_color, border_top)}>
            <div className={style.text} style={text}>{props.CT6}</div>
            <div className={`${style.sound} ${style.border_bottom_right_round}`} style={sound}>{props.NT6}</div>
            <CanvasDraw ref={canvasRef6} className={style.canvas} brushColor={ThemeMode === "dark" ? "rgba(250, 250, 250, 1)" : "#212121"} brushRadius={2.5} enablePanAndZoom={true} hideInterface={true} hideGrid={true} style={canvas} />
          </div>
          <div className={style.flex_container} style={Object.assign({}, background_color, border_top)}>
            <div className={style.text} style={text}>{props.CT7}</div>
            <div className={`${style.sound} ${style.border_bottom_right_round}`} style={sound}>{props.NT7}</div>
            <CanvasDraw ref={canvasRef7} className={style.canvas} brushColor={ThemeMode === "dark" ? "rgba(250, 250, 250, 1)" : "#212121"} brushRadius={2.5} enablePanAndZoom={true} hideInterface={true} hideGrid={true} style={canvas} />
          </div>
          <div className={`${style.flex_container} ${style.border_bottom_right_round}`} style={Object.assign({}, background_color, border_top)}>
            <div className={style.text} style={text}>{props.CT8}</div>
            <div className={`${style.sound} ${style.border_bottom_right_round}`} style={sound}>{props.NT8}</div>
            <CanvasDraw ref={canvasRef8} className={style.canvas} brushColor={ThemeMode === "dark" ? "rgba(250, 250, 250, 1)" : "#212121"} brushRadius={2.5} enablePanAndZoom={true} hideInterface={true} hideGrid={true} style={canvas} />
          </div>
        </div>
      </div>
    </Container>
  );
};

export default Component;
