import React from "react";
import Component from "../../function/original/Component_Original";
import Container from "../../function/Information";
import Data from "../../assets/data/Data_Original_jeokcheonsu";

const Original_jeokcheonsu22 = () => {
  const datas = Data.filter(num => num.id >= 69).filter(num => num.id <= 70);
  const components = datas.map((data, i) => <Component key={i} ST={data.id_} text1={data.OriginalText} text2={data.CommentaryText} text3={data.Meaning} />);

  return (
    <Container components={components} />
  );
};

export default Original_jeokcheonsu22;
