import React from "react";
import Component from "../../function/proverb/Component_Proverb";
import Container from "../../function/Information";
import Data from "../../assets/data/Data_Proverb";

const Proverb18 = () => {
  const datas = Data.filter(num => num.id >= 851).filter(num => num.id <= 897);
  const components = datas.map((data, i) => <Component key={i} ST={data.id} Proverb={data.Proverb} ProverbMeaning={data.ProverbMeaning} />);

  return (
    <Container components={components} />
  );
};

export default Proverb18;
