import React, { useEffect } from "react";
import { Link } from 'react-router-dom';
import style from "./css/DependentIndex.module.css"

const DependentIndex = () => {
  return (
    <div className={style.container}>
      <div className={style.flex1}>
        <Link to="/OriginalIndex" className={style.button_small}>원 문 공 부</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/StudyDependentIndex" className={style.button_small}>훈 음 공 부</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/WordDependentIndex" className={style.button_small}>단 어 공 부</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/IdiomDependentIndex" className={style.button_small}>사 자 성 어</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/ProverbIndex" className={style.button_small}>속 담 공 부</Link>
      </div>
    </div>
  );
};

export default DependentIndex;
