import React, { useEffect } from "react";
import { Link } from 'react-router-dom';
import style from "./css/MoreDependentIndex.module.css"

const WordDependentIndex = () => {
  return (
    <div className={style.container}>
      <div className={style.flex2}>
        <Link to="/WordIndex" className={style.button}>단 어 익 히 기</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/WordIndex_ReviewType" className={style.button}>단 어 복 습</Link>
      </div>
      <div className={style.flex2}>
        <Link to="/WordIndex_Writing" className={style.button}>단 어 쓰 기</Link>
      </div>
    </div>
  );
};

export default WordDependentIndex;
