import React from "react";
import { Link } from 'react-router-dom';
import style from "../css/MoreIndex_ReviewType.module.css"

const IdiomIndex_ReviewType = () => {
  return (
    <div className={style.container}>
      <div className={style.flex1}>
        <Link to="/IdiomIndex_ReviewA" className={style.button}>A type</Link>
      </div>
      <div className={style.flex1}>
        <Link to="/IdiomIndex_ReviewB" className={style.button}>B type</Link>
      </div>
    </div>
  );
};

export default IdiomIndex_ReviewType;
