import React from "react";
import Component from "../../function/original/Component_Original";
import Container from "../../function/Information";
import Data from "../../assets/data/Data_Original_sajasohag";

const Original_sajasohag4 = () => {
  const datas = Data.filter(num => num.id >= 31).filter(num => num.id <= 40);
  const components = datas.map((data, i) => <Component key={i} ST={data.id} text1={data.OriginalText} text2={data.CommentaryText} text3={data.Meaning} />);

  return (
    <Container components={components} />
  );
};

export default Original_sajasohag4;
