import React from "react";
import style from "./css/Preface.module.css"

const Preface = () => {
  const preface = `이 한자공부 앱은 천자문, 사자소학, 추구집, 삼자경, 계몽편, 학어집, 적천수 등의 앱들을 한꺼번에 모아서 원문 및 훈음, 단어, 사자성어, 속담을 종합하여 만든 앱입니다. 한자공부를 함에 있어서 방대한 내용들을 통하여 많은 향상이 있기를 기원하는 마음에서 출시하였습니다. 많은 도움이 되었으면 하는 마음에서 출시하였으니, 많은 이용 바랍니다.`;
  
  return (
    <div className={style.container}>
      <div className={style.text}>{preface}</div>
    </div>
  );
};

export default Preface;
