import React from "react";
import Component from "../../function/study/Component_Study";
import Container from "../../function/Information";
import Data from "../../assets/data/Data_Study";

const Study10 = () => {
  const datas = Data.filter(num => num.id >= 451).filter(num => num.id <= 500);
  const components = datas.map((data, i) => <Component key={i} ST={data.id} text1={data.CT} text2={data.NTB} text3={data.NT} />);

  return (
    <Container components={components} />
  );
};

export default Study10;
