import React from "react";
import Component from "../../function/word/Component_Word_ReviewA";
import Container from "../../function/Information";
import Data from "../../assets/data/Data_Word";

const Word_ReviewA1 = () => {
  const datas = Data.filter(num => num.id >= 1).filter(num => num.id <= 50);
  const components = datas.map((data, i) => <Component key={i} ST={data.id} CT={data.CT} NTB={data.NTB} NT={data.NT} Word1={data.Word1} Word2={data.Word2} Word3={data.Word3} WordSound1={data.WordSound1} WordSound2={data.WordSound2} WordSound3={data.WordSound3} WordMeaning1={data.WordMeaning1} WordMeaning2={data.WordMeaning2} WordMeaning3={data.WordMeaning3} CT1={data.CT1} CT2={data.CT2} CT3={data.CT3} NTB1={data.NTB1} NTB2={data.NTB2} NTB3={data.NTB3} NT1={data.NT1} NT2={data.NT2} NT3={data.NT3} />);

  return (
    <Container components={components} />
  );
};

export default Word_ReviewA1;
