import React, { useState } from "react";
import SwipeableViews from 'react-swipeable-views';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { useTheme } from "../ThemeProvider";
import style from "./css/Component_Original.module.css";
import Container from "../Container";
import TabText from "../swipeable_views/TabText";

const Component = (props) => {
  const [ThemeMode, toggleTheme] = useTheme();
  const [index, setIndex] = useState(0);
  const handleChange = (e, v) => setIndex(v);
  const handleChangeIndex = (i) => setIndex(i);
  const background_color = {backgroundColor: ThemeMode === "dark" ? "rgba(22, 22, 22, 1)" : "rgba(250, 250, 250, 1)"};
  const TabsStyle = {position: "absolute", top: 0, right: 0, width: "82%", height: 50, backgroundColor: ThemeMode === "dark" ? "rgba(32, 32, 32, 1)" : "rgba(240, 240, 240, 1)", borderBottomStyle: "solid", borderBottomColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderBottomWidth: ThemeMode === "dark" ? 1 : 2, borderTopRightRadius: 12};
  const TabIndicatorProps = {height: ThemeMode === "dark" ? 1.85 : 2.25, backgroundColor: ThemeMode === "dark" ? "rgba(200, 200, 200, 1)" : "rgba(200, 160, 12, 1)", marginBottom: ThemeMode === "dark" ? 1 : 0.75};
  const C = "tab_text";
  const S_i = {color: ThemeMode === "dark" ? "rgba(200, 200, 200, 0.8)" : "rgba(108, 108, 108, 0.8)"};
  const S_a = {color: ThemeMode === "dark" ? "rgba(200, 200, 200, 1)" : "rgba(200, 160, 12, 1)"};

  return (
    <Container>
      <div className={style.number} style={{backgroundColor: ThemeMode === "dark" ? "rgba(32, 32, 32, 1)" : "rgba(240, 240, 240, 1)", color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgba(128, 128, 128, 1)", borderBottomStyle: "solid", borderBottomColor: ThemeMode === "dark" ? "rgba(88, 88, 88, 1)" : "rgba(128, 128, 128, 1)", borderBottomWidth: ThemeMode === "dark" ? 1 : 2}}>{props.ST}</div>
      <Tabs value={index} fullWidth onChange={handleChange} style={TabsStyle} TabIndicatorProps={{style:TabIndicatorProps}}>
        <Tab label={index === 0 ? <TabText T="원문" C={C} S={S_a} /> : <TabText T="원문" C={C} S={S_i} />} className={style.tab_item} />
        <Tab label={index === 1 ? <TabText T="음" C={C} S={S_a} /> : <TabText T="음" C={C} S={S_i} />} className={style.tab_item} />
        <Tab label={index === 2 ? <TabText T="해설" C={C} S={S_a} /> : <TabText T="해설" C={C} S={S_i} />} className={style.tab_item} />
      </Tabs>
      <SwipeableViews index={index} enableMouseEvents onChangeIndex={handleChangeIndex} className="swipeable_views_container">
        <div className={style.container} style={background_color}>
          <div className={style.text1} style={{color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgb(85, 50, 0)"}}>{props.text1}</div>
        </div>
        <div className={style.container} style={background_color}>
          <div className={style.text2} style={{color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgba(185, 49, 22, 1)"}}>{props.text2}</div>
        </div>
        <div className={style.container} style={background_color}>
          <div className={style.text3} style={{color: ThemeMode === "dark" ? "rgba(235, 235, 235, 1)" : "rgba(86, 113, 04, 1)"}}>{props.text3}</div>
        </div>
      </SwipeableViews>
    </Container>
  );
};

export default Component;
